/**
 * Device information
 */
export interface UsageFormat {
  val: number;
  unit: string;
}
export interface RateFormat {
  rate: number;
  unit: string;
}
const USAGE_UNITS = ["B", "KB", "MB", "GB", "TB"];
const RATE_UNITS = ["kbps", "mbps", "gbps"];
export class IPDeviceData {
  inRate: number = 0;
  outRate: number = 0;
  constructor(
    //timestamp
    public timestamp: number,
    //device mac address
    public mac: string,
    //In case of one mac got several IPs, the one at index 0 should be the current assigned one
    public ip: Array<string>,
    //host name
    public hName: string,
    //given (configured name per mac)
    public givenName: string,
    //group willing GOD
    public group: string,
    //uploaded bytes
    public outBytes: number,
    //downloaded bytes
    public inBytes: number,
    //previous device data
    public prevData?: IPDeviceData
  ) {
    this._calcRates();
  }

  setData(
    //timestamp
    timestamp: number,
    //device mac address
    mac: string,
    //In case of one mac got several IPs, the one at index 0 should be the current assigned one
    ip: Array<string>,
    //host name
    hName: string,
    //given (configured name per mac)
    givenName: string,
    //group willing GOD
    group: string,
    //uploaded bytes
    outBytes: number,
    //downloaded bytes
    inBytes: number,
    //previous device data
    prevData?: IPDeviceData
  ): void {
    this.timestamp = timestamp;
    this.mac = mac;
    this.ip = ip;
    this.hName = hName;
    this.givenName = givenName;
    this.group = group;
    this.outBytes = outBytes;
    this.inBytes = inBytes;
    this.prevData = prevData;

    this._calcRates();
  }
  /**
   * Calculate in/out rates
   * @param prevData previous device data
   * @param deltaMs  time diff in milliseconds
   */
  private _calcRates() {
    if (this.prevData) {
      const delta = this.timestamp - this.prevData.timestamp;
      if (delta > 0) {
        this.inRate = ((this.inBytes - this.prevData.inBytes) * 8) / delta;
        this.outRate = ((this.outBytes - this.prevData.outBytes) * 8) / delta;
      }
    } else {
      this.inRate = 0;
      this.outRate = 0;
    }
  }

  getInBytes(): UsageFormat {
    let unit = 0;
    let val = this.inBytes;
    while (val > 0x400 && unit < USAGE_UNITS.length) {
      val = val / 0x400;
      unit++;
    }
    return { val: val, unit: USAGE_UNITS[unit] };
  }
  getOutBytes(): UsageFormat {
    let unit = 0;
    let val = this.outBytes;
    while (val > 0x400 && unit < USAGE_UNITS.length) {
      val = val / 0x400;
      unit++;
    }
    return { val: val, unit: USAGE_UNITS[unit] };
  }
  getTotalBytes(): UsageFormat {
    let unit = 0;
    let val = this.outBytes + this.inBytes;
    while (val > 0x400 && unit < USAGE_UNITS.length) {
      val = val / 0x400;
      unit++;
    }
    return { val: val, unit: USAGE_UNITS[unit] };
  }

  getInRate(): RateFormat {
    let unit = 0;
    let val = this.inRate / 1000;
    while (val > 1000 && unit < RATE_UNITS.length) {
      val = val / 1000;
      unit++;
    }
    return { rate: val, unit: RATE_UNITS[unit] };
  }

  getOutRate(): RateFormat {
    let unit = 0;
    let val = this.outRate / 1000;
    while (val > 1000 && unit < RATE_UNITS.length) {
      val = val / 1000;
      unit++;
    }
    return { rate: val, unit: RATE_UNITS[unit] };
  }
  getTotalRate(): RateFormat {
    let unit = 0;
    let val = (this.outRate + this.inRate) / 1000;
    while (val > 1000 && unit < RATE_UNITS.length) {
      val = val / 1000;
      unit++;
    }
    return { rate: val, unit: RATE_UNITS[unit] };
  }

  isInactive(): boolean {
    if (
      this.mac === "<incomplete>" &&
      this.inRate === 0 &&
      this.outRate === 0
    ) {
      return true;
    }
    return false;
  }
}
