<template>
  <div>
    <CRow>
      <!-- System Usage ---------------------------->
      <CCol
        sm="3"
        lg="3"
        style="height: 0px; padding-right: 5px; padding-left: 0px"
      >
        <CCard style="margin-top: 0">
          <CCardBody>
            <CRow>
              <CCol sm="5">
                <h4 id="sysinfo" class="card-title mb-0"></h4>
                <div class="small text-muted">System Information</div>
              </CCol>
            </CRow>
            <CRow>
              <SysDateTime />
            </CRow>
          </CCardBody>
        </CCard>
      </CCol>
      <!-- Realtime traffic line chart ---------------------------->
      <CCol sm="3" lg="6" style="padding-right: 5px; padding-left: 5px">
        <CCard style="margin-top: 0">
          <CCardBody>
            <div>
              <CRow>
                <CCol sm="5">
                  <h4 id="traffic" class="card-title mb-0"></h4>
                  <div class="small text-muted">Realtime Traffic</div>
                </CCol>
                <CCol sm="7" class="d-none d-md-block">
                  <CButtonGroup class="float-right mr-0">
                    <CButton
                      color="outline-secondary"
                      v-for="(value, key) in [
                        '1Minute',
                        '2Minutes',
                        '5Minutes',
                      ]"
                      :key="key"
                      class="mx-0"
                      :pressed="value === selectedTraffic ? true : false"
                      @click="selectedTraffic = value"
                    >
                      {{ value }}
                    </CButton>
                  </CButtonGroup>
                </CCol>
              </CRow>
              <IpTraffic style="height: 400px; margin-top: 0px" />
            </div>
          </CCardBody>
        </CCard>
      </CCol>
      <!-- Consumption ---------------------------->
      <CCol sm="3" lg="3" style="padding-right: 0px; padding-left: 5px">
        <CCard style="margin-top: 0">
          <CRow>
            <CCardBody>
              <CCardHeader> April </CCardHeader>
              <CRow style="margin-top: 0px">
                <CurrentMonth />
              </CRow>
            </CCardBody>
          </CRow>
          <CRow>
            <CCardBody>
              <CCardHeader> Monthly Subscription </CCardHeader>
              <CRow style="margin-top: 0px">
                <Subscription />
              </CRow>
            </CCardBody>
          </CRow>
        </CCard>
      </CCol>
    </CRow>

    <CRow>
      <!-- Devices data usage bar chart ---------------------------->
      <CCol md="9" style="padding-right: 5px; padding-left: 0px">
        <CCard>
          <CCardBody>
            <CRow>
              <CCol sm="5" style="padding-right: 0px; padding-left: 0px">
                <h4 class="card-title mb-0"></h4>
                <div class="small text-muted">Devices Usage</div>
              </CCol>
              <CCol sm="7" class="d-none d-md-block">
                <CButtonGroup class="float-right mr-0">
                  <CButton
                    color="outline-secondary"
                    v-for="(value, key) in usageQuickOptions"
                    :key="key"
                    class="mx-0"
                    :pressed="value === selectedDevices ? true : false"
                    @click="onlineDevicesSelectedPeriod(value)"
                  >
                    {{ value }}
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
            <DeviceUsage
              :ipDeviceUsageInstance="ipDeviceUsageInstance"
              style="height: 300px; margin-top: 0px"
            />
          </CCardBody>
        </CCard>
      </CCol>

      <!-- Consumption Details bar chart ---------------------------->
      <CCol md="3" style="padding-right: 0px; padding-left: 5px">
        <CCard>
          <CCardBody>
            <CRow>
              <CCol sm="5" style="padding-right: 2px; padding-left: 2px">
                <h4 id="traffic" class="card-title mb-0"></h4>
                <div class="small text-muted">Total Usage</div>
              </CCol>
              <CCol sm="7" class="d-none d-md-block">
                <CButtonGroup class="float-right mr-0">
                  <CButton
                    color="outline-secondary"
                    v-for="(value, key) in redUsageQuickOptions"
                    :key="key"
                    class="mx-0"
                    :pressed="value === selectedRedDetails ? true : false"
                    @click="redDetailsSelectedPeriod(value)"
                  >
                    {{ value }}
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
            <RedDetails
              :ipRedDetailsInstance="ipRedDetailsInstance"
              style="height: 300px; margin-top: 0px"
            />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>

    <!-- Devices details ---------------------------->
    <CRow>
      <CCol md="9" style="padding-right: 5px; padding-left: 0px">
        <CCard>
          <CCardHeader> Online Devices </CCardHeader>
          <CCardBody>
            <DeviceDetails
              :ipRealtimeTrafficInstance="ipRealtimeTrafficInstance"
            />
          </CCardBody>
        </CCard>
      </CCol>
      <!-- Devices groups ---------------------------->
      <CCol md="3" style="padding-right: 0px; padding-left: 5px">
        <CCard>
          <CCardHeader> Profiles </CCardHeader>
          <CCardBody>
            <DeviceGroups />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import { IPLive } from "@/iphome/realtime/IPLive";
import { IpTraffic } from "./realtime";
import { CurrentMonth, Subscription, RedDetails } from "./consumption";
import { DeviceDetails, DeviceGroups, DeviceUsage } from "./devices";
import { SysDateTime } from "./sysinfo";
import { IPDeviceUsage } from "@/iphome/IPDeviceUsage";
import { IPRedDetails } from "@/iphome/IPRedDetails";
import { IPRealtimeTraffic } from "@/iphome/realtime/IPRealtimeTraffic";
const USAGE_LAST_5_MINUTES = "5Minutes";
const USAGE_LAST_60_MINUTES = "60Minutes";
const USAGE_TODAY = "Today";
const USAGE_YESTERDAY = "Yesterday";
const USAGE_LAST_30_DAYS = "30Days";
const USAGE_YEAR = "Year";

export default {
  name: "Dashboard",
  components: {
    IpTraffic,
    DeviceUsage,
    DeviceDetails,
    DeviceGroups,
    CurrentMonth,
    Subscription,
    RedDetails,
    SysDateTime,
  },
  mounted() {
    //Initialize the live data provider
    this.ipLive = new IPLive();
    this.ipLive.onStatusChanged(this.liveStatusChanged);
    this.ipLive.start(false); //true to enabel simulation

    //Initialze the devices usage data provider (per device usuages bar chart)
    this.ipDeviceUsageInstance = new IPDeviceUsage();
    this.ipDeviceUsageInstance.retrieveUsageData("5", 1); //default to 5 minutes view

    //Initialize the total data usage (consumption bar chart)
    this.ipRedDetailsInstance = new IPRedDetails();
    this.ipRedDetailsInstance.retrieveUsageData("d", 10);

    //Initialize the realtime traffic monitor
    this.ipRealtimeTrafficInstance = new IPRealtimeTraffic(this.ipLive, 3);
  },
  destroyed() {
    this.ipLive.offStatusChanged(this.liveStatusChanged);
  },
  data() {
    return {
      selectedTraffic: "1Minute",
      selectedConsumption: "Month",
      selectedRedDetails: "Days",
      selectedDevices: "5Minutes",
      usageQuickOptions: [
        USAGE_LAST_5_MINUTES, //last 5 minutes
        USAGE_LAST_60_MINUTES, //this hour
        USAGE_TODAY, //usage since day start at 12:00AM
        USAGE_YESTERDAY, //usage for yesterday
        USAGE_LAST_30_DAYS, //usage for the last 30 days
        USAGE_YEAR, //usage for last 12 month
      ],
      redUsageQuickOptions: ["Hours", "Days", "Months"],
      ipRedDetailsInstance: {
        type: Object,
        default: undefined,
      },
      ipDeviceUsageInstance: {
        type: Object,
        default: undefined,
      },
      ipRealtimeTrafficInstance: {
        type: Object,
        default: undefined,
      },
      ipLive: {
        type: Object,
        default: undefined,
      },
    };
  },
  methods: {
    /**
     * Total Consumption bar chart Hours,Days,Months
     */
    redDetailsSelectedPeriod: function(val) {
      this.selectedRedDetails = val;
      var period = val.substr(0, 1).toLowerCase();
      var entries = 10;
      this.ipRedDetailsInstance.retrieveUsageData(period, entries);
    },
    /**
     * Usage per device bar chart 5Minutes,60Minutes, Today, Yesterday, 30Days, Year
     */
    onlineDevicesSelectedPeriod: function(val) {
      this.selectedDevices = val;
      var period = "5";
      var entries = 1;
      switch (val) {
        case USAGE_LAST_5_MINUTES:
          period = "5";
          entries = 1;
          break;
        case USAGE_LAST_60_MINUTES:
          period = "5";
          entries = 12;
          break;
        case USAGE_TODAY:
          period = "h";
          var dStart = new Date();
          var dNow = dStart.getTime();
          dStart.setSeconds(0);
          dStart.setMinutes(0);
          dStart.setHours(0);
          entries = (((dNow - dStart.getTime()) / 1000 / 3600) | 0) + 1; //deltaSeconds divided by secons/per hour, or with zero to truncate fractional
          console.log(entries);
          break;
        case USAGE_YESTERDAY:
          period = "d";
          entries = 1;
          break;
        case USAGE_LAST_30_DAYS:
          period = "d";
          entries = 30;
          break;
        case USAGE_YEAR:
          period = "d";
          entries = 365;
          break;
        default:
          console.error("Invalid period string!");
      }
      this.ipDeviceUsageInstance.retrieveUsageData(period, entries);
    },
    liveStatusChanged: function(_sender, status) {
      console.warn("Live data status changed:", status);
    },
  },
};
</script>
